.feature {
  display: flex;
  align-items: center;
  @include respond-to('m') {
    flex-direction: column; }

  &:not(:last-child) {
    margin-bottom: 3.2rem; }

  @include element('img-box') {
    width: 100%;
    margin-right: 3.2rem;
    min-width: 32rem;
    @include respond-to('m') {
      margin-right: 0;
      margin-bottom: 3.2rem;
      min-width: inherit; } }

  @include element('icon-box') {
    margin-bottom: 4rem;
    @include respond-to('m') {
      display: none; } } }
