.testimonials {
  display: flex;
  align-items: center;
  @include respond-to('l') {
    flex-direction: column; }

  @include element('thumbnails-box') {
    @include respond-to('l') {
      display: flex;
      width: 100%;
      justify-content: space-around;
      margin-bottom: 2.4rem; }
    @include respond-to('m') {
      justify-content: space-between; } }

  @include element('thumbnail') {
    background-color: $color-gray-scale-lighter;
    width: 12rem;
    height: 12rem;
    border-radius: 50%;
    position: relative;
    @include respond-to('m') {
      width: 8rem;
      height: 8rem; }
    @include respond-to('s') {
      width: 6.5rem;
      height: 6.5rem; }

    &:after {
      content: '';
      opacity: .5;
      transition: all $transition-duration-base $transition-duration-base;
      background: {
        size: contain;
        repeat: no-repeat;
        position: center 1rem; }
      position: absolute;
      border-radius: 50%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0; }

    &:hover:after {
      opacity: 1; }

    &:not(:last-child) {
      margin-bottom: 3.2rem; }

    &:nth-child(1):after {
      background-image: url('../img/testimonial-1.png'); }

    &:nth-child(2):after {
      background-image: url('../img/testimonial-2.png'); }

    &:nth-child(3):after {
      background-image: url('../img/testimonial-3.png'); }

    &:nth-child(4):after {
      background-image: url('../img/testimonial-4.png');
      background: {
        size: 95%;
        position: 1rem 1rem; } }

    &.is-active:after {
      opacity: 1;
      box-shadow: 0 0 0 .4rem $color-interactive; } }

  @include element('quote-box') {
    margin: 0 5%;
    max-width: 46%;
    @include respond-to('l') {
      max-width: inherit;
      margin-bottom: 3.2rem; }
    @include respond-to('m') {
      margin-left: 0;
      margin-right: 0; } }


  @include element('quote') {
    margin-bottom: 2.4rem; }

  @include element('quote-author-box') {
    display: flex;
    align-items: bottom; }

  @include element('mark') {
    margin-right: 1.6rem; }

  @include element('position') {
    @extend .label; }

  @include element('img') {
    transition: background $transition-duration-base $transition-timing-base;
    background: {
      image: url('../img/testimonial-1.png');
      size: contain;
      repeat: no-repeat;
      position: bottom center; }
    width: 40rem;
    height: 52rem;
    position: relative;
    min-width: 30rem;
    @include respond-to('m') {
      width: 25rem;
      height: 32rem;
      min-width: inherit; }

    &:after {
      content: '';
      display: block;
      position: absolute;
      height: 26rem;
      bottom: 0;
      width: 300%;
      background-color: $color-gray-scale-lighter;
      z-index: -1;
      @include respond-to('l') {
        left: -100%; }
      @include respond-to('m') {
        height: 16rem; } } } }
