*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  // This defines what 1rem is: 1 rem = 10px(10px/16px = 62.5%)
  font-size: 62.5%; }

body {
  box-sizing: border-box; }

::selection {
    background-color: $color-layout;
    color: $color-white; }

//media
img {
  max-width: 100%;
  height: auto; }
