.nav {
  @include element('item') {
    display: inline-block;

    &:not(:last-child) {
      margin-right: 4rem; } }

  @include element('link') {
    @extend .btn-text; } }

.footer .nav {
  @include element('item') {
    @include respond-to('m') {
      display: block; }

    &:not(:last-child) {
      @include respond-to('m') {
        margin-right: 0; } } }


  @include element('link') {
    @include respond-to('l') {
      margin-top: 2.4rem; } } }

.header .nav {
  @include respond-to('l') {
    position: absolute;
    width: 100%;
    top: 0;
    left: 0;
    padding: 6.4rem 0 0;
    background-color: $color-gray-scale-darker; }

  @include element('item') {
    @include respond-to('l') {
      display: block;
      height: 6.4rem;
      text-align: center; }

    &:not(:last-child) {
      @include respond-to('l') {
        margin-right: 0;
        border-bottom: .1rem solid $color-gray-scale-dark; } } }

  @include element('link') {
    width: 100%;
    height: 100%;
    line-height: 6.3rem; } }
