.h1 {
  font-size: 6.4rem;
  line-height: 7rem;
  position: relative;
  margin-bottom: 3.2rem;
  z-index: 2;
}

.h1::before, .h1::after {
  content: '';
  display: block;
  background-color: #FFCB06;
  position: absolute;
  transform: rotate(-45deg);
  transform-origin: center;
  height: .4rem;
  top: 3.2rem;
}

.h1::before {
  width: 10.4rem;
  top: 2.6rem;
  left: -2.1rem;
}

.h1::after {
  width: 7.2rem;
  top: 3.5rem;
  left: .4rem;
}

.h1::before, .h1::after {
  z-index: -1;
}

.h2 {
  font-size: 4.5rem;
  line-height: 5.4rem;
  margin-top: 22rem;
  margin-bottom: 6.4rem;
  position: relative;
  max-width: 48rem;
}

.h2::before, .h2::after {
  content: '';
  display: block;
  background-color: #FFCB06;
  position: absolute;
  transform: rotate(-45deg);
  transform-origin: center;
  height: .4rem;
  top: 3.2rem;
}

.h2::before {
  width: 10.4rem;
  top: 2.6rem;
  left: -2.1rem;
}

.h2::after {
  width: 7.2rem;
  top: 3.5rem;
  left: .4rem;
}

.h2::before, .h2::after {
  z-index: -1;
}

.h3 {
  font-size: 3.2rem;
  line-height: 3.8rem;
}

.h4 {
  font-size: 2.3rem;
  line-height: 2.9rem;
}

.p, .input__regular, .input__textarea, .map-text {
  font-size: 1.6rem;
  line-height: 2.8rem;
}

.p--bold {
  font-weight: 700;
  color: #19191A;
}

.p--white {
  color: #fff;
  text-shadow: 0 0 0.8rem rgba(25, 25, 26, 0.5);
}

.label, .testimonials__position {
  font-size: 1.2rem;
  line-height: 2.2rem;
}

.label--darker {
