  text-transform: uppercase;
  font-weight: 700;
}

.row {
  max-width: 48rem;
  padding: 0 1.6rem;
  margin: 0 auto;
}

.row::after {
  clear: both;
  content: "";
  display: block;
}

@media screen and (min-width: 48em) {
  .row {
    max-width: 124.8rem;
  }
}

.row [class^="col-"] {
  float: left;
  width: 100%;
}

.row [class^="col-"]:not(:last-child) {
  margin-right: 0;
  margin-bottom: 4rem;
}

@media screen and (min-width: 48em) {
  .row [class^="col-"]:not(:last-child) {
    margin-right: 3.2rem;
    margin-bottom: 0;
  }
}

.row .col-1-of-2 {
  width: calc((100% - 3.2rem) / 2);
}

@media screen and (max-width: 62em) {
  .row .col-1-of-2 {
    width: 100%;
  }
}

@media screen and (min-width: 48em) {
  .row .col-1-of-3 {
    width: calc((100% - 2 * 3.2rem) / 3);
  }
}

@media screen and (min-width: 48em) {
  .row .col-2-of-3 {
    width: calc(2 * ((100% - 2 * 3.2rem) / 3) + 3.2rem);
  }
}

@media screen and (min-width: 48em) {
  .row .col-1-of-4 {
    width: calc((100% - 3 * 3.2rem) / 4);
  }
}

@media screen and (min-width: 48em) {
  .row .col-2-of-4 {
    width: calc(2 * ((100% - 3 * 3.2rem) / 4) + 3.2rem);
  }
}

@media screen and (min-width: 48em) {
  .row .col-3-of-4 {
    width: calc(3 * ((100% - 3 * 3.2rem) / 4) + 2 * 3.2rem);
  }
}

