.header__icon.is-active .header__hamburger:after {
  top: 0;
  transform: rotate(135deg);
}

.header__icon.is-active .header__hamburger:before {
  top: 0;
  transform: rotate(45deg);
}

.header__hamburger {
  width: 100%;
  height: .4rem;
  position: absolute;
  position: absolute;
  top: 50%;
  transform: translate(0, -50%);
