}

.btn, .input__submit {
  padding: 2rem 4rem;
  border-radius: .2rem;
  position: relative;
  background-color: #EF3740;
}

.btn:after, .input__submit:after {
  content: '';
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 80%;
  z-index: -1;
  box-shadow: 0 0 1.6rem 0 rgba(239, 55, 64, 0.2), 0 0 2.4rem 0 rgba(25, 25, 26, 0.2);
  height: 50%;
}

.btn:hover, .input__submit:hover {
  background-color: #a7272d;
}

.btn:active, .input__submit:active {
  background-color: #781c20;
  transform: scale(0.98);
}

.btn:active:after, .input__submit:active:after {
  width: 75%;
  transform: scale(0);
  box-shadow: 0 0 0.2rem 0 rgba(239, 55, 64, 0.2), 0 0 2rem 0 rgba(25, 25, 26, 0.2);
}

.btn-text:link, .nav__link:link, .btn-text:visited, .nav__link:visited {
