  background-color: rgba(200, 202, 204, 0.5);
}

.tabs-table__tab.is-active, .is-active.tabs-table__tab-accordion:link {
  color: #19191A;
  font-weight: 700;
}

.tabs-table__tab.is-active:after, .is-active.tabs-table__tab-accordion:link:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: .4rem;
  background-color: #EF3740;
}

.tabs-table__tab-accordion:link {
  width: 100%;
  background-color: #F5F8FA;
  display: none;
  border-bottom: 1px solid #C8CACC;
}

@media screen and (max-width: 48em) {
  .tabs-table__tab-accordion:link {
    display: block;
  }
}

.tabs-table .tabs-table__tab-accordion:last-of-type {
  border-bottom: none;
}

.session {
  padding: 3.2rem 6.4rem;
  display: flex;
  align-items: center;
}

@media screen and (max-width: 48em) {
  .session {
    flex-direction: column;
    align-items: flex-start;
    padding: 3.2rem 0;
  }
}

.session:not(:last-child) {
  border-bottom: 1px solid #C8CACC;
}

.session__hour-and-icon {
  margin-right: 8rem;
  text-align: center;
}

@media screen and (max-width: 48em) {
  .session__hour-and-icon {
    margin-bottom: 2.4rem;
  }
}

.session__hour {
  color: #19191A;
  text-shadow: none;
  background-color: #F5F8FA;
  padding: .8rem 3.2rem;
  margin-top: 3.2rem;
}

.session__details {
  display: flex;
}

@media screen and (max-width: 62em) {
  .session__details {
    flex-direction: column;
  }
}

.session__detail {
  display: flex;
}

.session__detail :last-child {
  margin-left: .8rem;
}

.session__detail:not(:last-child) {
  margin-right: 6.4rem;
}

@media screen and (max-width: 62em) {
  .session__detail:not(:last-child) {
    margin-right: 0;
    margin-bottom: .8rem;
  }
}

.prices-panels-box {
  display: flex;
}

@media screen and (max-width: 62em) {
  .prices-panels-box {
    display: block;
    max-width: 48rem;
  }
}

.price-panel {
  padding: 3.2rem;
  width: calc(100% / 3);
  border-top: 0.4rem solid #FFCB06;
  position: relative;
}

@media screen and (max-width: 62em) {
  .price-panel {
    width: 100%;
  }
}

.price-panel:after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: block;
  z-index: -10;
  background-color: #F5F8FA;
}

.price-panel:not(:last-child) {
  margin-right: 3.2rem;
}

@media screen and (max-width: 62em) {
  .price-panel:not(:last-child) {
    margin-bottom: 3.2rem;
  }
}

.price-panel__price {
  display: flex;
  align-items: flex-end;
  margin-bottom: 6.4rem;
}

.price-panel__price .h1 {
  margin: 0;
  position: relative;
  top: .3rem;
  margin-right: .8rem;
}

.price-panel__price .h1::after, .price-panel__price .h1::before {
  display: none;
}

.price-panel__popular {
  background-color: #FFCB06;
  padding: .8rem;
  top: 3.4rem;
  text-shadow: none;
  position: absolute;
  right: 0;
}

.price-panel__features-and-btn {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

@media screen and (max-width: 75em) {
  .price-panel__features-and-btn {
    min-height: 26rem;
  }
}

@media screen and (max-width: 62em) {
  .price-panel__features-and-btn {
    min-height: inherit;
  }
}

.map-box {
  position: absolute;
  top: 0;
  height: 100%;
  width: 40%;
  right: 0;
}

@media screen and (max-width: 62em) {
  .map-box {
    bottom: -115%;
    top: auto;
    left: 0;
    width: 100%;
  }
}

#map {
  width: 100%;
  height: 100%;
}

.section-contact__box, .section-testimonials, .section-benefits {
  position: relative;
}

.section-testimonials, .section-benefits {
  overflow: hidden;
}

.section-testimonials {
  padding-bottom: .4rem;
}

.section-cta {
  background-color: #F5F8FA;
  position: relative;
  padding: 12rem 0;
  margin-top: 22rem;
}

.section-cta__img {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  width: 40%;
  background-image: url("../img/cta-training-women.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center top;
}

@media screen and (max-width: 62em) {
  .section-cta__img {
    width: 100%;
    opacity: .2;
    z-index: 1;
  }
}

.section-contact {
  margin-bottom: 22rem;
}

@media screen and (max-width: 62em) {
  .section-contact {
    margin-bottom: 60rem;
  }
}

[class^='section-'] {
  margin-left: auto;
  margin-right: auto;
  max-width: 220rem;
}
