.footer {
  @include respond-to('m') {
    text-align: center; }
  //svg
  svg {
    transition: all $transition-timing-base $transition-duration-base;
    fill: $color-gray-scale-darker; }

  svg:hover {
    fill: $color-interactive; }

  @include element('upper-box') {
    background-color: $color-gray-scale-darker;
    padding: 4rem;

    //not a good practice to have a two level selector...
    .row {
      display: flex;
      align-items: center;
      @include respond-to('l') {
        flex-direction: column;
        align-items: flex-start; }
      @include respond-to('m') {
        align-items: center; } } }

  @include element('logo-box') {
    margin-right: auto;
    @include respond-to('m') {
      margin-right: 0; } }

  @include element('lower-box') {
    background-color: $color-layout;
    padding: 1.6rem 0;

    //not a good practice to have a two level selector...
    .row {
      display: flex;
      align-items: center;
      @include respond-to('m') {
        flex-direction: column; } } }


  @include element('social-box') {
    margin-left: auto;
    @include respond-to('m') {
      margin-left: 0;
      margin-top: 1.6rem; } }

  @include element('social-link') {

    &:not(:last-child) {
      margin-right: 4.8rem; } } }
